import React, { useEffect, useState } from 'react'
import { CustomTabs } from '../../../reuseableComponents'
import EmpOverview from '../EmpOverview';
import SalaryDetails from '../SalaryDetails';
import InvestmentNotFound from './InvestmentNotFound';
import { useDispatch, useSelector } from 'react-redux';
import BackIcon from '../../../commanComponent/BackIcon';
import { Link, useNavigate } from 'react-router-dom';
import { CustCheckbox, CustomStrong, Icon, Input, Label } from '../../../commanComponent';
import { payrollActions } from '../../../../core/resource/actions';
import { getInputPrefix, toTitleCase } from '../../../../utils/common';
import { useCustomTranslation } from '../../../../translation';

const Investments = () => {
    const { t } = useCustomTranslation()
    const {
        selectedEmployeeData,

        getEmployeeInvestmentPending,
        getEmployeeInvestmentRes,
        getEmployeeInvestmentError,

    } = useSelector(state => state.payroll)
    const [investmentType, setInvestmentType] = useState("itDeclaration")
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const [itDeclarationData, setItDeclarationData] = useState('')
    const tabsRequest = [
        { name: "Overview", id: "overview" },
        { name: "Salary Details", id: "salaryDetails" },
        { name: "Investments", id: "investments" },
    ];
    const investmentTabs = [
        { name: "Pending", id: "pending" },
        { name: "Approved ", id: "approved" },
    ];
    const handleInvestmentTabChange = (type) => {
        setInvestmentType(type)
        if (type === "itDeclaration") {
            navigate("/payroll-employees/employee-detail/it-declaration")
        }
        else if (type === "proofOfInvestment") {
            navigate("/payroll-employees/employee-detail/proof-of-Investment")
        }
    }

    useEffect(() => {
        getEmployeeItDeclaration()
    }, [])

    const getEmployeeItDeclaration = () => {
        const payload = {
            _id: selectedEmployeeData?._id
        }
        dispatch(payrollActions.getEmployeeInvestmentRequest(payload))
    }

    useEffect(() => {
        if (!getEmployeeInvestmentPending) {
            if (getEmployeeInvestmentError) {
                return;
            }
            if (getEmployeeInvestmentRes) {
                if (getEmployeeInvestmentRes?.code === 200) {
                    setItDeclarationData(getEmployeeInvestmentRes?.data?.[0])
                }
            }
        }
    }, [
        getEmployeeInvestmentRes,
        getEmployeeInvestmentError,
        getEmployeeInvestmentPending
    ]);
    const renderIncomeRows = (data, labelMap) => {
        return Object.entries(labelMap).map(([key, label]) =>
            data[key] ? (
                <tr key={key}>
                    <td className="ps-4">{t(label)}</td>
                    <td>₹{data[key]}</td>
                </tr>
            ) : null
        );
    };

    const renderedPreviousEmployment = () => {
        const { allowances_declarations: previousEmployment } = itDeclarationData;

        return previousEmployment?.map((item, index) => (
            <React.Fragment key={index}>
                <tr className="fw-bold">
                    <td colSpan="2">Previous Employment</td>
                </tr>
                {renderIncomeRows(item, {
                    afterExemptions: "labels.afterExemptions",
                    incomeTax: "labels.incomeTax",
                    professionalTax: "labels.professionalTax",
                    employeeProvidentFund: "labels.employeeProvidentFund",
                    leaveEncashmentExemptions: "labels.leaveEncashmentExemptions",
                })}
                <tr>
                    <td colSpan="2">
                        <hr />
                    </td>
                </tr>
            </React.Fragment>
        ));
    };

    const renderedOtherSourceOfIncome = () => {
        const { other_sources_of_income: otherSourceIncome } = itDeclarationData;

        return otherSourceIncome?.map((item, index) => (
            <React.Fragment key={index}>
                <tr className="fw-bold">
                    <td colSpan="2">Other Income</td>
                </tr>
                {renderIncomeRows(item, {
                    otherSources: "labels.incomeOtherSources",
                    savingsDeposit: "labels.incomeOtherSources",
                    fixedDeposit: "labels.fixedDeposit",
                    nationalIncome: "labels.savingsCertificates",
                })}
                <tr>
                    <td colSpan="2">
                        <hr />
                    </td>
                </tr>
            </React.Fragment>
        ));
    };



    return (
        <div className="cardBox  myRequest p-4">
            <div className='d-flex mb-3'>
                <div >
                    <Link to={"/payroll-employees"}>
                        <BackIcon />
                    </Link>
                </div>
                <div>
                    <h5 >
                        {selectedEmployeeData?.name}
                    </h5>
                </div>
                <div className='ms-2 mt-1'>
                    <span className='span-label'>Emp.ID:{selectedEmployeeData?.emp_id}</span>
                </div>
            </div>

            <CustomTabs tabs={tabsRequest} type="investment" ulClass={"requesttab"} />

            <div className="tab-content">
                <div className="tab-pane fade show active" id="overview">
                    <EmpOverview
                        selectedEmployeeData={selectedEmployeeData}
                    />
                </div>
                <div className="tab-pane fade" id="salaryDetails">
                    <SalaryDetails />
                </div>
                <div className="tab-pane fade" id="investments">
                    <div className="requestdata">
                        <div className="d-flex align-items-center mb-4">
                            <ul className="nav nav-tabs leaveTabs ms-3">
                                <li className="nav-item">
                                    <button
                                        className="nav-link active"
                                        data-bs-toggle="tab"
                                        data-bs-target="#itDeclaration"
                                        type="button"
                                    // onClick={() => handleInvestmentTabChange("itDeclaration")}
                                    >
                                        IT Declaration
                                        {/* <span>4</span> */}
                                    </button>
                                </li>
                                <li className="nav-item">
                                    <button
                                        className="nav-link"
                                        data-bs-toggle="tab"
                                        data-bs-target="#proofOfInvestment"
                                        type="button"
                                    // onClick={() => handleInvestmentTabChange("proofOfInvestment")}
                                    >
                                        Proof of Investments
                                    </button>
                                </li>

                            </ul>
                        </div>
                        <div className="tab-content">
                            <div className="tab-pane fade show active " id="itDeclaration">

                                {
                                // itDeclarationData 
                                false? <div className='ms-3 '>
                                    <div className='d-flex gap-2 justify-content-between'>
                                        <h5 >
                                            IT Declaration
                                            <span
                                                className={`badge greenBadge ms-2`}
                                            >
                                                Declared
                                            </span>

                                        </h5>
                                        <div className="btn-group actionDrp">
                                            <button type="button" className="btn dropdown-toggle-split"
                                                data-bs-toggle="dropdown" aria-expanded="false">
                                                <i className="bi bi-three-dots-vertical"></i>
                                            </button>
                                            <ul className="dropdown-menu">
                                                <li>
                                                    <span className="dropdown-item"
                                                        onClick={() => handleInvestmentTabChange("itDeclaration")}
                                                    >
                                                        <Icon className={"fa fa-edit test"} /> Update</span>
                                                </li>
                                                {/* <li>
                                                    <span className="dropdown-item"
                                                        onClick={() => handleInvestmentTabChange("itDeclaration")}
                                                    >
                                                        <Icon className={"fa fa-eye"} /> View</span>
                                                </li> */}


                                            </ul>
                                        </div>
                                    </div>
                                    <div className="alert " role="alert" style={{ background: "rgb(247, 248, 255)" }}>
                                        <Icon className={"bi bi-info-circle-fill"}
                                            style={{ color: "rgb(43, 118, 210)" }} />
                                        Tax Regime : <CustomStrong
                                            text={toTitleCase(itDeclarationData?.tax_regime)}
                                        />

                                    </div>
                                    <div className='mt-4'>
                                        <table class="table " style={{ border: "snow", fontSize: "16px" }}>
                                            <thead class="table-light">
                                                <tr>
                                                    <th>PARTICULARS</th>
                                                    <th>DECLARED AMOUNT</th>
                                                </tr>
                                            </thead>
                                            <tbody>

                                                {renderedOtherSourceOfIncome()}
                                                {renderedPreviousEmployment()}

                                            </tbody>
                                        </table>
                                    </div>
                                </div> :
                                    <InvestmentNotFound
                                        handleChange={handleInvestmentTabChange}
                                        type={"itDeclaration"}
                                        headingText={"IT Declaration submission is locked for this employee"}
                                        paragraphText={"You can either allow the employee to submit IT Declaration through the portal or submit it on their behalf"}
                                    />}
                            </div>
                            <div className="tab-pane fade" id="proofOfInvestment">
                                <div className='ms-3 '>
                                    <div className='d-flex gap-2 justify-content-between'>
                                        <h5 >
                                            Proof Of Investment
                                            <span
                                                className={`badge blueBadge ms-2`}
                                            >
                                                Draft
                                            </span>

                                        </h5>
                                        <div className="btn-group actionDrp">
                                            <button type="button" className="btn dropdown-toggle-split"
                                                data-bs-toggle="dropdown" aria-expanded="false">
                                                <i className="bi bi-three-dots-vertical"></i>
                                            </button>
                                            <ul className="dropdown-menu">
                                                <li>
                                                    <span className="dropdown-item"
                                                        onClick={() => handleInvestmentTabChange("proofOfInvestment")}
                                                    >
                                                        <Icon className={"fa fa-edit test"} /> Update</span>
                                                </li>
                                                <li>
                                                    <span className="dropdown-item"
                                                        onClick={() => handleInvestmentTabChange("proofOfInvestment")}
                                                    >
                                                        <Icon className={"fa fa-eye"} /> View</span>
                                                </li>


                                            </ul>
                                        </div>
                                    </div>
                                    <div className="alert " role="alert" style={{ background: "rgb(247, 248, 255)" }}>
                                        <Icon className={"bi bi-info-circle-fill"}
                                            style={{ color: "rgb(43, 118, 210)" }} />
                                        Tax Regime :{"New Tax Regime"}
                                    </div>
                                    <div className='mt-4'>
                                        <table class="table " style={{ border: "snow", fontSize: "16px" }}>
                                            <thead class="table-light">
                                                <tr>
                                                    <th>PARTICULARS</th>
                                                    <th>DECLARED AMOUNT</th>
                                                    <th>PROOFS</th>
                                                    <th>COMMENTS</th>
                                                    <th>ACTUAL AMOUNT</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr class="fw-bold">
                                                    <td colspan="5">Other Income</td>
                                                </tr>
                                                <tr>
                                                    <td class="ps-4">Income from other sources</td>
                                                    <td>₹0.00</td>
                                                    <td><a href="#">📎 1</a></td>
                                                    <td>📝</td>
                                                    <td>₹3,34,339.00</td>
                                                </tr>
                                                <tr>
                                                    <td class="ps-4">Interest Earned from Savings Deposit</td>
                                                    <td>₹0.00</td>
                                                    <td><a href="#">📎</a></td>
                                                    <td>📝</td>
                                                    <td>₹5,45,550.00</td>
                                                </tr>
                                                <tr>
                                                    <td colspan="5"><hr /></td>
                                                </tr >
                                                <tr class="fw-bold">
                                                    <td colspan="5">Net Income / Loss from House Property</td>
                                                </tr>
                                                <tr>
                                                    <td class="ps-4">Total Income / Loss from House Property</td>
                                                    <td>₹0.00</td>
                                                    <td>-</td>
                                                    <td>-</td>
                                                    <td>₹161.00</td>
                                                </tr>
                                                <tr>
                                                    <td class="ps-4">➕ Net Income / Loss from Let Out Property - 1</td>
                                                    <td>₹0.00</td>
                                                    <td>-</td>
                                                    <td>-</td>
                                                    <td>₹-161.00</td>
                                                </tr>
                                                <tr>
                                                    <td colspan="5"><hr /></td>
                                                </tr >
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                                <InvestmentNotFound
                                    handleChange={handleInvestmentTabChange}
                                    type={"proofOfInvestment"}
                                    headingText={"POI submission is locked for this employee"}
                                    paragraphText={"You can either allow the employee to submit POI through the portal or submit the investment proofs on their behalf"}
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Investments
