import React from 'react'
import { CustomStrong, Icon, Label } from '../../commanComponent'
import CustomTooltip from '../../commanComponent/Tooltip'

const SalaryDetails = () => {
    return (
        <div>
            <div className="cardHead">
                <h3>
                    Salary Details
                </h3>
            </div>
            <div style={{ width: "80%" }}>
                <div className="formPanel">
                    <div className="row">
                        <div className="col-md-4 d-flex ">
                            <div className="form-group mb-2">
                                <Label text={"Annual CTC"} />
                                <p>
                                    <CustomStrong text={"₹ 40,000.00 per year"}
                                        style={{ fontSize: "18px" }}
                                    />
                                </p>
                            </div>
                        </div>

                        <div className="col-md-4">
                            <div className="form-group mb-2">

                                <div className="form-group mb-2">
                                    <Label text={"Monthly CTC"} />
                                    <p>
                                        <CustomStrong text={"₹ 3,333.00 per month"}
                                            style={{ fontSize: "18px" }}
                                        />
                                    </p>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-4" style={{ textAlign: "right",marginTop: "30px" }}>
                            <div className="form-group mb-2">

                                <div className="form-group mb-2">
                                    <span className='salary-print'>
                                        <CustomTooltip text={"Print Salary Certification"} element={<i class="bi bi-printer"></i>} />
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
                <div className='cardBox'>
                <div className="container">
                    <div className="row">
                        <div className="col-md-4">
                            <label >SALARY COMPONENTS</label>
                        </div>
                        <div className="col-md-4" style={{ textAlign: "right" }}>
                            <label>MONTHLY AMOUNT</label>
                        </div>
                        <div className="col-md-4" style={{ textAlign: "right" }}>
                            <label>ANNUAL AMOUNT</label>
                        </div>
                    </div>
                    <hr />
                    <div className="row mb-4">
                        <div className="col-md-4">
                            <CustomStrong text={"Earnings"} style ={{fontSize: "15px"}}/>
                        </div>
                    </div>
                    <div className="row mb-3">
                        <div className="col-md-4">
                        <CustomStrong text={"Basic"} />
                            <p>(50.00% of CTC)</p>
                            
                        </div>
                        <div className="col-md-4" style={{ textAlign: "right" }}>
                            <p>₹ 1,667.00</p>
                        </div>
                        <div className="col-md-4" style={{ textAlign: "right" }}>
                            <p>₹ 20,004.00</p>
                        </div>
                    </div>
                    <div className="row mb-3" >
                        <div className="col-md-4" >
                        <CustomStrong text={"House Rent Allowance"} />
                            <p>(50.00% of Basic Amount)</p>
                        </div>
                        <div className="col-md-4"style={{ textAlign: "right" }}>
                            <p>₹ 834.00</p>
                        </div>
                        <div className="col-md-4" style={{ textAlign: "right" }}>
                            <p>₹ 10,008.00</p>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-4">
                        <CustomStrong text={"Fixed Allowance"} />
                        </div>
                        <div className="col-md-4" style={{ textAlign: "right" }}>
                            <p>₹ 832.00</p>
                        </div>
                        <div className="col-md-4" style={{ textAlign: "right" }}>
                            <p>₹ 9,984.00</p>
                        </div>
                    </div>
                    <hr />
                    <div className="row">
                        <div className="col-md-4">
                            <h5>Cost to Company</h5>
                        </div>
                        <div className="col-md-4" style={{ textAlign: "right" }}>
                            <h5>₹ 3,333.00</h5>
                        </div>
                        <div className="col-md-4" style={{ textAlign: "right" }}>
                            <h5>₹ 40,000.00</h5>
                        </div>
                    </div>
                </div>
                </div>

            </div>
        </div>
    )
}

export default SalaryDetails
